//Import Dependencies Here
import React, { Component } from 'react';
import Request from 'superagent';
import GlobalVars  from '../statics/GlobalVars';
import history from '../routes/History';

class FormLogin extends Component {

    constructor(props) {
        super(props);
        this.state = {
            formValues: {
                email: '',
                password : ''
            }
        }
    }

    handleChange(event) {
        event.preventDefault();
        let formValues = this.state.formValues;
        let key = event.target.name;
        let value = event.target.value;
        formValues[key] = value;
        this.setState({formValues});
    }

    handleSubmit(event) {
        event.preventDefault();
        console.log(this.state.formValues);  
        
        Request.post(GlobalVars.hostname+":"+GlobalVars.port+"/auth/sign_in")
            .send({"email":"broker0@mail.com","password":"password"})
            .set('Content-Type', 'application/json')
            .end((error, response) => {
            if(response.statusCode === 200){
                console.log(response);
                this.setSessionLoginParameter(response.header);
                history.push('/home');
                window.location = "/home";
                // Redirect Now to the Home Page
            }else{
                console.log(error);
            }
        });
    }

    setSessionLoginParameter(header){
        localStorage.clear();
        localStorage.setItem('access-token', header['access-token']);
        localStorage.setItem('client', header['client']);
        localStorage.setItem('expiry', header['expiry']);
        localStorage.setItem('uid', header['uid']);
    }

    render() {
        return (
            <form className="login-form bs-reset" id="new_user" onSubmit={this.handleSubmit.bind(this)} >
                <div className="row mb-4">
                    <div className="col-md-6">
                        <label htmlFor="user_email">Email</label>
                        <input className="form-control login-email-field" id="signin_email" type="email"  name="email" value={this.state.formValues["email"]} onChange={this.handleChange.bind(this)} required/>
                    </div>
                    <div className="col-md-6">
                        <label htmlFor="user_password">Password</label>
                        <input className="form-control login-password-field " id="signin_password" type="password"  name="password" value={this.state.formValues["password"]} onChange={this.handleChange.bind(this)}  required/>
                    </div>
                    </div>
                    
                    <div className="row">
                    <div className="col-sm-4">
                        <label className="rememberme mt-checkbox mt-checkbox-outline">
                            <input type="checkbox" name="remember" value="1"/> Remember me
                            <span></span>
                        </label>
                    </div>
                    <div className="col-sm-8 text-right">
                        <div className="forgot-password">
                            <a href="/" id="forgot-password-button" className="forgot-password-button">Forgot Password?</a>
                        </div>
                        <input type="submit" name="commit" value="Sign In" className="btn btn-info" data-disable-with="Sign In" />
                    </div>
                    <br/>
                    <div className="forgot-password">
                        <a href="/">New to Loanerr? Click here to sign up.</a>
                    </div>
                </div>
        </form>
        );
    }
}

export default FormLogin;



