//Import Dependencies Here
import React, { Component } from 'react';
import Request from 'superagent';
import GlobalVars  from '../statics/GlobalVars';

class FormSignUp extends Component {
    constructor(props) {
        super(props);
        this.state = {
            formValues: {
                firstname: '',
                lastname : '',
                email : '',
                role : '',
                password : '',
                password_confirmation : ''
            }
        }
    }

    handleChange(event) {
        event.preventDefault();
        let formValues = this.state.formValues;
        let key = event.target.name;
        let value = event.target.value;
        formValues[key] = value;
        this.setState({formValues});
    }

    handleSubmit(event) {
        event.preventDefault();
        console.log(this.state.formValues);  

        Request.post(GlobalVars.hostname+":"+GlobalVars.port+"/auth")
            .send({"email":"willlll@mail.com","password":"password"})
            .set('Content-Type', 'application/json')
            .end((error, response) => {
            if(response.statusCode === 200){
                console.log(response);
                // Redirect Now to the Login Page Once Successfully
            }else{
                console.log(error);
            }
        });
  
    }

    render() {
        return (
            <form  className="new_user" id="new_user"  onSubmit={this.handleSubmit.bind(this)} >
                <input name="utf8" type="hidden" value="✓" />
                <input type="hidden" name="authenticity_token" value="Z8TgOOZhBcUfpPV9psIwENeRAxzOab9J6D8EYDVAGXqRyQ0ydc5gq514a3ZWY9CMySzCfETGTRpOvNsszzKyTA==" />
                <div className="form-group">
                    <input placeholder="First Name" className="form-control" id="signup_first_name" type="text" name="firstname" value={this.state.formValues["firstname"]} onChange={this.handleChange.bind(this)} required />
                </div>
                <div className="form-group">
                    <input placeholder="Last Name" className="form-control" id="signup_last_name" type="text" name="lastname" value={this.state.formValues["lastname"]} onChange={this.handleChange.bind(this)} required />
                </div>
                <div className="form-group">
                    <input placeholder="Email" className="form-control" id="signup_email" type="email" name="email" value={this.state.formValues["email"]} onChange={this.handleChange.bind(this)}  required/>
                </div>
                <div className="form-group">
                    <label htmlFor="user_role">Account Type</label>
                    <select className="form-control" name="role" id="user_role" value={this.state.formValues["role"]} onChange={this.handleChange.bind(this)}  >
                        <option value="broker">Broker</option>
                        <option value="realtor">Realtor</option>
                        <option value="solicitor">Solicitor</option>
                    </select>
                </div>
                <div className="form-group">
                    <input placeholder="Password" className="form-control" id="signup_password" type="password" name="password" value={this.state.formValues["password"]} onChange={this.handleChange.bind(this)} required />
                </div>
                <div className="form-group">
                    <input placeholder="Confirm password" className="form-control" id="signup_password_confirmation" type="password" name="password_confirmation" value={this.state.formValues["password_confirmation"]} onChange={this.handleChange.bind(this)} required />
                </div>
                <hr/>
                <input type="submit" name="commit" value="Sign Up" className="btn btn-primary float-right" data-disable-with="Sign Up"/>
                </form>
        );
    }
}

export default FormSignUp;



